import "./css/findPw.css"
import React, { useState, useEffect } from 'react';
import axios from 'axios'

function FindPw() {
	const [email, setInputEmail] = useState("");
	// const [phone, setInputPhone] = useState("");

	const handleInputEmail = (e) => {
		setInputEmail(e.target.value);
	};

	// const handleInputPhone = (e) => {
	// 	setInputPhone(e.target.value);
	// };

	return (
		<div className="FindPwForm">
			<div className="navLogo" onClick={(e) => {clickBtn("/", e)}}>
        		<span className="navLogo">CAPSTONE </span>
        		<span className="navLogo" id="LogoColor">DESIGN</span>
      		</div>
			<div className="FindPwTitle"><span> </span></div>
			<div className="InputId">
				{/*     FindPwLast   */}
	  			<input type="text" path="email" placeholder="" id="email" name="email" value={email} onChange={handleInputEmail} />
	  		</div>
			<div className="FindPwSubTitle"><span>    .</span></div>
			<div className="FindPwNextBtn" onClick={(e) => {clickBtn("FindPw", e)}}> </div>
			<div className="FindPwNotice">
				<span>       .</span>
				<span>       .</span>
				<span>      .</span>
			</div>
		</div>
	);
}

const clickBtn = (params, e) => {
    console.log(params);
    if(params == "/") {
        document.location.href = "/";
    } else if(params == "FindPw") {
        document.location.href = "/findPwLast";
    }
};

export default FindPw;
