import "./css/login.css";
import React, { useState, useEffect, useRef } from "react";
import axios from "axios";
import Swal from "sweetalert2";

function Login() {
  const [email, setInputEmail] = useState("");
  const [pass, setInputPw] = useState("");

  const emailRef = useRef("");
  const passRef = useRef("");

  const handleInputEmail = (e) => {
    setInputEmail(e.target.value);
  };

  const handleInputPw = (e) => {
    setInputPw(e.target.value);
  };

  const onKeyPressLogin = (e) => {
    if(e.key == 'Enter') {
      onClickLogin();
    }
  }
  const onClickLogin = (e) => {
    console.log("click login");
    console.log("Email : ", email);
    console.log("PW : ", pass);

    if(emailRef.current.value == '') {
      alert("  ");
      emailRef.current.focus();
    } else if(passRef.current.value == '') {
      alert("  ");
      passRef.current.focus();
    } else {
        axios
        .post("/login", {
          email: email,
          pass: pass,
        })
        .then((res) => {
          console.log(res);
          console.log("res.data.userEmail :: ", res.data.email);
          console.log("res.data.uno :: ", res.data.uno);
          if (res.data.email === undefined) {
            // id    userId = undefined, msg = ' id   .'
            console.log("======================", res.data.msg);
            // alert("    .");
            Swal.fire({
              icon: 'error',
              title: '    .',
              // timer: 100000,
          }).then((q) => {
            if(q.isConfirmed){
              document.location.href = "/login";
            }
          }

          );

            
          } else if (res.data.email === null) {
            // id , pw    userId = null , msg = undefined
            console.log(
              "======================",
              "    ."
            );
            Swal.fire({
              icon: 'error',
              title: '    .',
              // timer: 100000,
          }).then((q) => {
            if(q.isConfirmed){
              document.location.href = "/login";
            }
          }

          );
              

          } else if (res.data.email === email) {
            // id, pw   userId = userId1, msg = undefined
            console.log("======================", " ");
          Swal.fire({
            icon: 'success',
            title: ' !',
            // timer: 100000,
        }).then((q) => {
          if(q.isConfirmed){
            sessionStorage.setItem("id", res.data.email); // sessionStorage id user_id key  
            sessionStorage.setItem("nickname", res.data.nickname);
            //     ()
            document.location.href = "/";
          }
        }

        );
          }
        })
        .catch();
    }
  };
  
  return (
    <div className="LoginForm">
      <div className="navLogo" onClick={clickLogo}>
        <span className="navLogo">CAPSTONE </span>
        <span className="navLogo" id="LogoColor">
          DESIGN
        </span>
      </div>
      <div className="InputId">
        <input
          type="text"
          path="email"
          placeholder=""
          name="email"
          value={email}
          onChange={handleInputEmail}
          onKeyPress={(e)=>onKeyPressLogin(e)}
          ref={emailRef}
        />
      </div>
      <div className="InputPw">
        <input
          type="password"
          path="pass"
          placeholder=""
          name="pass"
          value={pass}
          onChange={handleInputPw}
          onKeyPress={(e)=>onKeyPressLogin(e)}
          ref={passRef}
        />
      </div>
      <div className="LoginBtn" onClick={onClickLogin}>
        
      </div>
      <div className="LoginOption">
        <ul className="option-list">
          <li className="list-item-text">
            <a href="/findId" id="findId">
               
            </a>
          </li>
          <li className="list-item-text">
            <a href="/findPw" id="findPw">
               
            </a>
          </li>
          <li className="list-item-text">
            <a href="/joinFirst" id="signUp">
              
            </a>
          </li>
        </ul>
      </div>
      <div className="hr-sect"></div>
      <div className="social">
        <ul className="social-login">
          <li className="list-item-img">
            <a href="#" id="google-login">
              <div className="social-icon">
                <img
                  className="google-icon"
                  src={`${process.env.PUBLIC_URL}/btn_google_signin_light_normal_web@2x.png`}
                  alt=""
                />
              </div>
            </a>
          </li>
        </ul>
      </div>
    </div>
  );
}

const clickLogo = () => {
  document.location.href = "/";
};

export default Login;
