// import React from "react";
// import "./css/reset.css"
import "./css/item.css"
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import select from "./Select";

const FrozenItem = props => {
  //     
  const [testStr, setTestStr] = useState('');

  //  
  function callback(str) {
    setTestStr(str);
  }

  //      
  useEffect(
    () => {
      axios({
        url: '/Refrigerator',
        method: 'GET'
      }).then((res) => {
        callback(res.data);
      })
    }, []
  );

  function test() {
    alert("DB     ")
  }
  function test2() {
    alert("")
  }

  // 
  const { img, expiration, name, num, day } = props;

  return (
    <div>
      {props.frozen && props.frozen.map((item, i) => (

        <div className="item" key={i}>
          <div className="img"></div>
          <div className="info">
            <div className="first">
              <div className="date"><input className="expiration" type={"date"} /> </div>
              <div className="del" onClick={test2}></div>
              <div className="chek" onClick={test}></div>
            </div>
            <div className="second">
              <div className="proName"> <input className="nameInput" type={Text} /></div>
              <div className="num">
                 <div className="numOpsion">
                  <input className="numInput" type={Text} />
                  <select>
                    <option value={"sum"}></option>
                    <option value={"kg"}>kg</option>
                    <option value={"ml"}>ml</option>
                  </select>
                </div>
              </div>
            </div>
            <div className="dead">  : {day}</div>
          </div>
        </div>
      ))}

    </div>

  );
}

export default FrozenItem;