import Header from "./Header";
import Footer from "./Footer";
import Layout from './Menubar';
import Kan from './Kan';
import Select from './Select'
import React, { useState, useEffect } from 'react';
import axios from 'axios';

function Refrigerator_main() {

  //     
  const [testStr, setTestStr] = useState('');

  //  
  function callback(str) {
    setTestStr(str);
  }

  //      
  useEffect(
    () => {
      axios({
        url: '/Refrigerator',
        method: 'GET'
      }).then((res) => {
        callback(res.data);
      })
    }, []
  );

  return (
    <div className="Refrigerator">
      <Header />
      <Layout />
      <Kan />
      <Select />
      <Footer />
      {testStr}
    </div>
  );
}

export default Refrigerator_main;