import React, { useState } from "react"
// import "./css/reset.css"
import "./css/kan.css"
import ColdItem from "./ColdItem.js"
import FrozenItem from "./FrozenItem.js"
import RoomItem from "./RoomItem.js"

const Kan = () => {
    //        <Food/>   //popup props  .aa   

    const [cold, coldList] = useState([0])

    const AddColdItem = () => {
        let countArr = [...cold]
        let counter = countArr.slice(-1)[0]
        counter += 1
        countArr.push(counter)	// index  X
        // countArr[counter] = counter	// index     	
        coldList(countArr)
    }

    const [frozen, frozenList] = useState([0])
    const AddFrozenItem = () => {
        let countArr = [...frozen]
        let counter = countArr.slice(-1)[0]
        counter += 1
        countArr.push(counter)	// index  X
        // countArr[counter] = counter	// index     	
        frozenList(countArr)
    }

    const [room, roomList] = useState([0])
    const AddRoomItem = () => {
        let countArr = [...room]
        let counter = countArr.slice(-1)[0]
        counter += 1
        countArr.push(counter)	// index  X
        // countArr[counter] = counter	// index     	
        roomList(countArr)
    }

    return (
        <div className="fir">
            <div className="kan">
                <div className="cold-border">
                    <div className="namePlus">
                        <p className="name"></p>
                        <a className="plus" onClick={AddColdItem}></a>
                    </div>

                    <div className="cold" id="cold">
                        <ColdItem cold={cold} />
                        {/* {cold} */}
                    </div>
                </div>

                <div className="frozen-border">
                    <div className="namePlus">
                        <p className="name"></p>
                        <a className="plus" onClick={AddFrozenItem}></a>
                    </div>
                    <div className="frozen" id="frozen">
                        <FrozenItem frozen={frozen} />
                    </div>
                </div>

                <div className="room-border">
                    <div className="namePlus">
                        <p className="name"></p>
                        <a className="plus" onClick={AddRoomItem}></a>
                    </div>
                    <div className="room" id="room">
                        <RoomItem room={room} />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Kan;