import "./css/header.css"
import "./css/reset.css"
import React, { useState, useEffect } from 'react';

function Header() {
	const [isLogin, setIsLogin] = useState(false); // 
	useEffect(() => {
		if (sessionStorage.getItem("id") === null) {
			// sessionStorage  name  key    
			console.log("isLogin ?? :: ", isLogin);
		} else {
			// sessionStorage  name  key    
			//   
			setIsLogin(true);
			console.log("isLogin ?? :: ", isLogin);
		}
	});


	return (
		<div className="Header">
			<nav className="navbar">
				<div className="navLogo" onClick={clickLogo}>
					<span className="navLogo">CAPSTONE </span>
					<span className="navLogo" id="LogoColor">DESIGN</span>
				</div>
				<ul className="navMenu">
					{/*     */}
					{isLogin ? (<li><a className="menu" href="/myfridge">{" "}</a></li>)
						: (<li></li>)}
					<li><a className="menu" href="/recipe">{''}</a></li>
					<li><a className="menu" href="/daily_meals">{''}</a></li>
					{isLogin ? <li><a className="menu" href="/mypage">{""}</a></li> : <li></li>}
					{isLogin ? (<li><a className="menu" onClick={() => { sessionStorage.clear() }} href="/">{''}</a></li>)
						: (<li><a className="menu" href="/login">{''}</a></li>)}
				</ul>
			</nav>
		</div>
	);
}

const clickLogo = () => {
	document.location.href = '/';
}

export default Header;